@use "sass:math";

// ブレイクポイントの設定
// ＊＊＊＊＊SP・PCファーストについて、この設定を変更します＊＊＊＊＊
$break-flg: 1 !default; //0:SPファースト、1：PCファースト


// レイアウト幅
$layout-width-inner: 1440px;

// ブレークポイント
// 該ブレークポイントから次のブレークポイントまでの範囲が、それぞれの領域にあたる。
// 例) 実機iphoneSEは375pxのため、413px以下〜375p以上になるよう、値を指定。
$sp-the-most-small: 374px;
$iphone-se: 413px;
$sp-breakpoint: 767px;
$ipad-mini: 819px;
$ipad: 1023px;
$tab: #{$layout-width-inner - 1px};
$wide-breakpoint: 1440px;

$breakpoints: null;
@if $break-flg == 0 {
  $breakpoints: (
    // ディスプレイ幅
    "ip-se": "screen and (min-width: 375px)",
    "sp": "screen and (min-width: 414px)",
    "ipad-mini": "screen and (min-width: 768px)",
    "ipad": "screen and (min-width: 820px)",
    "iab": "screen and (min-width: 1024px)",
    "pc": "screen and (min-width: #{layout-width-inner})",
    // 解像度
    "twice": "screen and (-webkit-min-device-pixel-ratio: 1.5)",
    "three-times": "screen and (-webkit-min-device-pixel-ratio: 2.5)",
    // 解像度andディスプレイ幅
    "wide":
      "screen and (min-width: #{$wide-breakpoint + 1px}) and (-webkit-max-device-pixel-ratio: 1.1)"
  );
} @else {
  $breakpoints: (
    // ディスプレイ幅
    "xxs": "screen and (max-width: 374px)",
    "xs": "screen and (max-width: 413px)",
    "sp": "screen and (max-width: 767px)",
    "ipad": "screen and (max-width: 820px)",
    "ipad-pro": "screen and (max-width: 1024px)",
    "tab": "screen and (max-width: #{layout-width-inner - 1px})",
    // 解像度
    "twice": "screen and (-webkit-min-device-pixel-ratio: 1.5)",
    "three-times": "screen and (-webkit-min-device-pixel-ratio: 2.5)",
    // 解像度andディスプレイ幅
    "wide":
      "screen and (min-width: #{$wide-breakpoint + 1px}) and (-webkit-max-device-pixel-ratio: 1.1)"
  );
}



$breakpoints: null;
@if $break-flg == 0 {
  $breakpoints: (
    "sm": "screen and (min-width: 400px)",
    "md": "screen and (min-width: 768px)",
    "lg": "screen and (min-width: 1000px)",
    "xl": "screen and (min-width: 1200px)",
  );
} @else {
  // ブレイクポイントの設定
  $breakpoints: (
    "sm": "screen and (max-width: 399px)",
    "md": "screen and (max-width: 767px)",
    "lg": "screen and (max-width: 999px)",
    "xl": "screen and (max-width: 1199px)",
  );
}

// inner
$inner: 1200px;

// 余白
$padding-pc: 25px; // PC用余白設定
$tab-tab: 25px; // タブレット用余白設定
$padding-sp: 20px; // スマホ用余白設定

// フォント
$font-family-base: 'Noto Sans JP', sans-serif; // ベースフォント設定

$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;

// ベースフォントサイズ
$base-font-size: 1rem;

// Color
$color-black:#333;
$color-red:#e00;
$color-theme: #2589d0;
$color-white: #fff;
